html {
  --color-text: black;
  --color-border-light: #d3d3d3;
  --color-light-bg: #f3f3f3;
  --color-border-dark: #2f2f2f;
  --color-primary: #09915A;
  --color-primary-wop: rgba(9, 145, 90, 0.04);
  --color-font-secondary: #505050;
  --gutter: 16px;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ====================
   RESUME SECTION START 
   ==================== */

.resume-section {
  padding: 5px;
  margin-bottom: 5px;
}

.resume-section:hover {
  outline: 1px dashed rgb(90, 89, 89);
  border-radius: 4px;
  /* background-color: rgb(250, 250, 250); */
}

.component-dragging {
  outline: 1px dashed rgb(90, 89, 89);
  border-radius: 4px;
  background-color: rgb(250, 250, 250);
}

.resume-section-title {
  display: block;
  margin: 0 0 10px 0;
  border-bottom: 3px solid #000;
  font-size: 24px;
  font-weight: 700;
}

.full-width-field {
  width: 100%;
  display: block;
}

.resume-section-wrap {
  position: relative;
}

.drag-handle {
  display: none;
  position: absolute;
  right: 0;
  top: 10px;
  color: var(--color-font-secondary);
}

.resume-section-wrap:hover .drag-handle, .component-dragging .drag-handle {
  display: block;
}

.resume-paper-content-draggin-over {
  background-color: #e6ffe9;
}

/* ====================
   RESUME SECTION END 
   ==================== */

.d-none {
  display: none !important;
}   

@media screen and (max-width: 480px) {
  .mobile-d-none {
    display: none !important;
  }
}